.container-onGameProfile{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 7vw;
    min-width: 40px;
    height: 17vh;
}


@media (max-height:300px){
    .container-onGameProfile{
        height: 16vh;
    }
}

@media (max-height:600px){
    .container-onGameProfile{
        height: 20vh;
    }
}

@media (max-height:500px){
    .box-playerInfo{
        font-size: 1.8vh;
    }
    .progressBar{
        font-size: 1.8vh;
    }
} 


@media (max-width:600px){
    .container-onGameProfile{
        height: 15vh;
    }
}

@media (max-width:700px){
    .container-onGameProfile{
        height: 20vh;
    }

}



.box-status{
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;

    width:100%;
    height: 20%;
    padding: 0 10% 0 10%;
    box-shadow: 0  0 20px black;
    border-radius: 20px;
    background-color: rgb(0, 0, 0);
    z-index: 1;
    box-shadow: inset 0 0 10px 1px rgb(0, 132, 255),
                0 0 10px 1px rgb(0, 132, 255);
    border-style: solid;
    border-width: 2px;
    border-color: aliceblue;
}

.box-statusText{
    font-size: 1vw;
    z-index: -1;
    color:rgb(255, 255, 255);
}
.box-statusText.Fold{
    color: rgb(255, 255, 255);
}

.box-status.Fold{
    filter: brightness(30%);
    box-shadow: inset 0 0 10px 1px rgb(0, 0, 0),
                0 0 10px 1px rgb(0, 0, 0);
    background-color: rgb(0, 0, 0) ;
}
.box-status.Waiting{
    box-shadow: inset 0 0 10px 1px rgb(0, 255, 98),
                0 0 10px 1px rgb(0 , 255, 98);
}
.box-status.Raised{
    box-shadow: inset 0 0 10px 1px rgb(255, 0, 0),
                0 0 10px 1px rgb(255, 0, 0);
}
.box-status.Empty{
    box-shadow: inset 0 0 10px 0px rgb(255, 255, 255),
    0 0 10px 0px rgb(255, 255, 255);
    filter: brightness(30%);
}
.profilePic.Fold{
    filter:saturate(0);
    filter: brightness(40%);
}
.profilePic.Empty{
    
    filter: blur(3px);
}
.box-playerInfo.Fold{
    color: rgb(88, 88, 88);
}





.progressBar{
    position: absolute;
    display: flex;
    align-self: center;

	overflow: hidden;
	width: 100%;
	height: 100%;
	border-radius: 20px;
	background-color: #000000;
    filter: opacity(100%);
    top:0;
    left: 0;
}

.progressPercent{
    position: absolute;
    align-self: center;

	font-weight: 600;
	left: 50%;
	top: 50%;
	transform: translate(-50%,-50%);
	color: #00a674;
	text-shadow: 0px 0px 10px #ffffff;
}



.box-playerInfo{
    display:flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    margin-top: 10%;

    height:40%;
    width: 100%;
    padding: 1vw 0.5vw 1vw 0.5vw;

    border-radius: 20% 20% 100% 100%;

    font-size: 1vw;

    border-style: solid;
    border-color: #000000;
    border-width: 1px;
    color: rgb(198, 198, 198);
    background-color: rgba(0, 0, 0, 0.5);
    box-shadow:  0 0 20px 10px black;
}

.profilePic{
    position: absolute;
    bottom: 0;
    border-radius:50%;
    width:3.3vw;
    border-style:solid;
    border-width: 0.3vw;
    border-color: black;
    box-shadow: 0 0 10px black;
}


.showCardPlayers1 img ,.showCardPlayers2 img{
    width:2.5vw;
    height: auto;
    position: absolute;
    box-shadow: -2px 2px 5px  black;
}
.showCardPlayers1 , .showCardPlayers2{
    opacity: 0%;
    transform: rotate(-20deg);
    transform-origin: left bottom;
    position: absolute;
    transition: transform 1s ease, opacity 1s ease 0.5s; 
}
.playerCardAppear{
    opacity: 100;
}
.showCardPlayers1{
    left: 0;
    bottom: 30%;
}
.showCardPlayers2{
    left: 15%;
    bottom: 30%;
}
.flippedPlayerCards{
    transform: rotateY(180deg);
    transform-origin: center;
    transition: transform 2s ease;
    transform-style: preserve-3d;
}

